* {
    font-family: Helvetica s, Arial, sans-serif;
}
body {
    padding: 60px;
    background-color: cyan;
}
input {
    font-size: 1.2rem;
    width: 20%;
}
/* si se ocupa*/
.card-grid {
    display: inline-flex;
    flex-direction: row;
    flex-wrap: wrap;
    max-width: 100%;
}

/* se usa*/
.img-gif {
    max-width: 30%;
    border: 1px solid black;
    margin: 10px;
    background-color: white;
}
.card-grid p {
    padding: 5px;
    text-align: center;
}
img {
    max-width: 100%;
}
/* se coucpa*/
.alineado-gif {
    float: left;
    margin: 5px;
    border: 2px solid ;
    background-color: white;


}
.alineado-gif p {
    margin: 10px;
}
button {
    background-color: #4CAF50;
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
}
.formato {
    display: inline-flex;
}
.espacio{
    margin: 5px;
    cursor: pointer;
}
